<template>
  <div class="text-center" id="app">
    <h2>Multi-languages Translator</h2>
    <h5>Developed by Jun</h5>
    <TranslateForm v-on:formSubmit="translateText"></TranslateForm>
    <TranslateOutput v-text="translatedText"></TranslateOutput>
  </div>
</template>

<script>
import TranslateForm from './components/TranslateForm'
import TranslateOutput from './components/TranslateOutput';
export default {
  name: 'app',
  components: {
    TranslateForm,
    TranslateOutput
  },
  data:function(){
    return{
     translatedText:''
    }
  },
  methods: {
    translateText: function(text,language){
      this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20171015T083129Z.fd6e696c33f7e088.b4cefa6e9e35382599d9e68c75fd74c8ac4d3374&lang='+language + '&text='+text)
      .then((response) =>{
        this.translatedText= response.body.text[0];
      });
    }

  }
}
</script>

<style>
body {
  background: #fefefe;
  padding-top: 100px;
}
h5{padding-top: 10px;}
</style>



